export default {
	components: {},
	data() {
		return {
			items: [
				"新订单",
				"待配送",
				"催单"
			],
			height: '',
			current: 0,
			orders: [],
			top: '',
			latitude: '',
			longitude: '',
			tg: true,
			opens: -1,
			merchantId: '',
			wait_orders: [],
			newOrders_page: 1,
			wait_delivery_page: 1,
			nomore: true,
			index: '',
			shopname:'',

		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.shopname=e.data.name;
				that.down_newOrders();
				that.down_delivery();
				// that.reminder();
			}
		});
	},
	onReady() {
		this.getTopheight();
	},
	onShow() {},
	// 下拉刷新
	onPullDownRefresh() {
		if (this.index == 0) {
			this.down_newOrders();
		} else if (this.index == 1) {
			this.down_delivery();
		}

		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	// 上拉加载
	onReachBottom() {
		if (this.index == 0) {
			this.newOrders_page++;
			this.load_newOrders();
		} else if (this.index == 1) {
			this.wait_delivery_page++;
			this.wait_delivery();
		}

	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.top = 65 + "px";
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
		onTap: function(index) {
			this.index = index;
			if (this.current != index) {
				this.current = index;
				this.$forceUpdate();
			};

		},
		// 生成新订单
		load_newOrders: function() {
			let that = this;
			uni.showLoading();
			uni.request({
				url: this.serveipd + '/api/merchant/order/newOrders',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
					page: that.newOrders_page,
				},
				success: res => {
					uni.hideLoading();
					let neworder = []
					neworder = res.data.data;
					that.orders = that.orders.concat(neworder);
					if (res.data.data.length == 0) {
						uni.showToast({
							title: "没有更多数据了",
							icon: 'none'
						});
						return
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 新订单下拉刷新
		down_newOrders: function() {
			let that=this
			uni.request({
				url: that.serveipd + '/api/merchant/order/newOrders',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
				},
				success: res => {
					that.orders = res.data.data;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 待配送下拉刷新
		down_delivery: function() {
			let that=this
			uni.request({
				url: that.serveipd + '/api/merchant/order/delivery',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
				},
				success: res => {
					that.wait_orders = res.data.data;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//待配送列表
		wait_delivery: function() {
			let that = this;
			uni.showLoading();
			uni.request({
				url: that.serveipd + '/api/merchant/order/delivery',
				method: 'GET',
				data: {
					merchantId: that.merchantId,
					page: that.wait_delivery_page
				},
				success: res => {
					uni.hideLoading();
					let waitorder = []
					waitorder = res.data.data;
					that.wait_orders = that.wait_orders.concat(waitorder);
					console.log(res);
					if (res.data.data.length == 0) {
						uni.showToast({
							title: "没有更多数据了",
							icon: 'none'
						});
						return
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 催单
		reminder: function() {
			let that = this;
			uni.request({
				url: that.serveipd + '/api/merchant/order/merchantNews',
				method: 'GET',
				data: {
					merchantId: that.merchantId
				},
				success: res => {
					console.log(res);
				}
			});
		},
		wz: function(latitude, longitude) {
			uni.openLocation({
				latitude: latitude,
				longitude: longitude,
				success: function() {}
			});
		},
		dh: function(phone) {
			uni.makePhoneCall({
				phoneNumber: phone,
				success: function(res) {}
			});
		},
		open: function(index) {
			this.tg = !this.tg;
			if (this.opens != index) {
				this.opens = index;
			} else {
				this.opens = -1;
			}
		},
		// 立即接单
		receive: function(orderno) {
			let that = this;
			uni.request({
				url: this.serveipd + "/api/merchant/order/newOrder",
				method: 'GET',
				data: {
					orderno: orderno,
					merchantId: that.merchantId,
				},
				success(res) {
					uni.showToast({
						title: res.data.msg,
						duration: 2000
					});
					that.down_newOrders();
				}
			});
		},
		// 确认配送
		confirm: function(orderno) {
			let that = this;
			uni.request({
				url: this.serveipd + "/api/merchant/order/confirmDelivery",
				method: 'GET',
				data: {
					orderno: orderno,
				},
				success(res) {
					uni.showToast({
						title: res.data.msg,
						duration: 2000
					});
					that.down_delivery();
				}
			});
		},
	}
}
